@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div.flex-container-user-menu {
    display: flex;
}

div.flex-child-avatar {
    flex: 0 0 20%;
}

div.flex-child-username {
    flex: 0 0 60%;
}

#link-main:hover {
    text-transform: none;
}
li.user-item {
    font-size: 20px;
    margin: 10px;
}

button.delete-user-button {
    background-color: green;
    color: white;
    font-size: 16px;
    margin: 2px;
    text-align: center;
    border-radius: 10px;
    opacity: 0.8;
}

button.delete-user-button:hover {
    opacity: 1;
}

div.user-block {
    display: inline;
}

div.admin-container {
    margin-left: 20px;
}

div.back-to-home-admin {
    font-size: 25px;
}

button.logout-button-admin {
    background-color: #6495ED;
    color: white;
    font-size: 20px;
    opacity: 0.8;
    padding: 2px 5px;
    margin: 10px;
    border-radius: 10px;
}

button.logout-button:hover {
    opacity: 1;
}
html, body {
    height: 100%;
}

div.home-container {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

div.flex-container {
    width: 100%;
    height: 100%;
    /* border: 3px solid red; */
    display: flex;
    flex-direction: row;
}

div.home-sidebar {
    flex: 0 0 20%;
    margin: 0;
    overflow: auto;
    background-color: #f1f1f1;
    /* border: 3px solid black; */
}

div.home-main {
    flex: 0 0 80%;
    text-align: center;
    height: 100%;
    /* border: 3px solid green; */
}

h3.room-header {
    padding: 0 15px;
    font-size: 22px;
    cursor: pointer;
}

ul.room-list {
    list-style-type: none;
    padding: 0 15px;
}

ul.contact-list {
    list-style-type: none;
    padding: 0 15px;
}

.username-logo {
    margin-right: 8px;
}

li.room-item {
    width: 100%;
    color: black;
    font-size: 20px;
    margin: 5px 0px;
    padding: 2px 5px;
}

li.active, li.room-item:hover {
    background-color: #949494;
    width: 96%;
    color: white;
    cursor: pointer;
}

div.chat-close-button-container {
    float: right;
    position: relative;
}

a.chat-close-button {
    opacity: 0.3;
    color: white;
    text-decoration: none;
    margin: auto;
    border: none;
}

a.chat-close-button::before {
    content: '\00d7';
}

a.chat-close-button:hover {
    opacity: 1;
}

div.chat-close-button-container:hover span.chat-close-button-tooltip {
    visibility: visible;
}

span.chat-close-button-tooltip {
    visibility: hidden;
    background-color: black;
    width: 100px;
    font-size: 15px;
    text-align: center;
    padding: 2px 2px;
    border-radius: 5px;
    position: absolute;
    /* z-index: 1; */
    bottom: 100%;
    right: 0;
}

li.new-room-item {
    width: 60%;
    color: black;
    font-size: 20px;
    margin: 5px 0px;
    padding: 2px 5px;
}

li.new-room-item:hover {
    cursor: pointer;
    text-decoration: underline;
}

div.main-room-header {
    background-color: #949494;
    padding: 1px 10px;
    margin: 0;
    min-height: 69px;
    color: white;
    /* border: 3px solid black; */
    position: relative;
    border-bottom: 1px solid black;
}

div.main-room-header h2:hover {
    cursor: pointer;
}

/* Settings button: Placed realative to main room header */
div.settings-div {
    position: absolute;
    right: 26px;
    top: 20px;
}

button.settings-dropdown-btn {
    font-size: 20px;
    padding: 5px;
    border-radius: 5px;
    border: none;
    background-color: #949494;
    color: white;
    display: inline-block;
}

button.settings-dropdown-btn:hover {
    cursor: pointer;
    background-color: rgb(0, 182, 255);
}

div.dropdown-content {
    display: none;
    margin: 5px 0;
    background-color: #f1f1f1;
    position: absolute;
    right: 0;
    min-width: 16em;
    z-index: 1;
}

div.dropdown-content a {
    color: black;
    text-decoration: none;
    padding: 10px;
    display: block;
    text-align: left;
}

div.dropdown-content a:hover {
    background-color: #ddd;
    cursor: pointer;
}

div.dropdown-show {
    display: block;
}
div.chat-room-container {
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
    margin: 0;
}

div.messages-container {
    min-height: 500px;
    height: 85%;
    overflow: auto;
    /* border: 3px solid black; */
    border-radius: 7px 7px 0 0;
    margin-right: 20px;
    margin-bottom: 10px;
    margin-left: 0;
}

span.username-stamp {
    font: small;
    width: 35%;
    margin: 12px;
    padding: 15px 15px;
}

div.single-message-container {
    width: 25%;
    margin: 8px;
}

div.my-message {
    margin-left: auto;
}

div.received-message {
    margin-right: auto;
}

.messages-list {
    list-style-type: none;
    padding: 0;
}

.new-message-input-field {
    max-height: 50%;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    padding: 8px 12px;
    resize: none;
    height: 25px;
    width: 95%;
}

li.message-item {
    word-break: break-word;
    padding: 15px 15px;
    margin: 5px;
    border-radius: 15px;
    color: white;
}

li.my-message {
    background-color: rgb(0, 132, 255);
    margin-left: auto;
}

li.received-message {
    background-color: #232529;
    margin-right: auto;
}

.messages-container, .new-message-input-field, .send-message-button {
    border-color: #9a9a9a;
}

div.user-typing-msg {
    margin: 20px;
}

div.user-joined-left-msg {
    margin: 20px;
    text-align: center;
}

div.button-div {
    display: inline;
    float: right;
    margin-bottom: 10px;
}

/* Right sidebar */

div.sidebar-right-container {
    flex: 0 0 25%;
    background-color: #f1f1f1;
}

div.sidebar-right-header {
    background-color: #949494;
    padding: 1px 10px;
    min-height: 69px;
}


div.right-sidebar-contact-item {
    display: flex;
}

div.right-sidebar-avatar-container {
    flex: 0 0 15%;
    margin-left: 20px;
    align-items: center;
}

div.right-sidebar-username-container {
    flex: 0 0 50%;
    text-align: left;
}

div.right-sidebar-username-container p {
    font-size: 20px;
    position: relative;
    bottom: 15px;
    
}

div.right-sidebar-username-container:hover {
    cursor: pointer;
}
/* Close button for the dialogs */
#new-group-form-dialog-close-button {
    position: absolute;
    right: 1px;
    top: 1px;
}

/* Workaround to remove additional input box appearing for some reason? */
#group-name-text-field {
    border: 0px solid black;
}

#stepper-button {
    text-transform: none;
    margin: 0 5px;
}
div.search-area-container {
    margin: auto;
    width: 50%;
}

/* Workaround to remove additional input box appearing for some reason? */
#group-search-box {
    border: 0px solid black;
}


h1.greeting {
    font-size: 45px;
    text-align: center;
    color: white;
    padding-top: 2em;
    margin-top: 0;
    margin-bottom: 20px;
}

p.greeting {
    font-size: 25px;
    text-align: center;
    color: black;
    margin-top: 10px;
}

div.register-login-button-container {
    text-align: center;
    margin: auto;
    width: 100%;
    display: inline-block;
}

.home-nav-item {
    text-decoration: none;
    color: white;
}

.header-button {
    font-size: 25px;
    background-color: #111111;
    color: white;
    text-align: center;
    text-decoration: none;

    display: inline;
    margin: 1em;
    padding: 10px;
    border-radius: 20px;
    width: 15%;
    opacity: 0.85;
}

.header-button:hover {
    opacity: 1;
    cursor: pointer;
}

#signIn {
    background-color: #111111;
}

#login-button {
    background-color: #5f5f5f;
}

#hidden {
    visibility: hidden;
    margin: 2em;
}

#header {
    background-color: #6495ED;
}
#footer-div {
    background-color: #202020;
    display: inline-block;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: center;
    margin-top: 100px;
    margin-bottom: 0;
    width: 100%;
}

#footer-div ul li {
    display: inline;
}

.footer-items li a {
    color: white;
    text-decoration: none;
    text-align: center;
    padding: 10px;
    font-weight: bold;
    font-size: 18px;
}

.footer-items a:hover {
    background-color: #111111;
}
#sectionOne {
    text-align: center;
    float: right;
    width: 30%;
    margin-right: 100px;
    margin-top: 100px;
}

h2.sectionOne-title {
    text-align: center;
    float: right;
    font-weight: bold;
    font-size: 40px;
    width: 100%;
}

#sectionTwo {
    text-align: center;
    float: left;
    margin-left: 12em;
    margin-top: 80px;
    width: 35%;
}

h2.sectionTwo-title {
    text-align: center;
    float: left;
    font-weight: bold;
    font-size: 40px;
    width: 100%;
}

p.desc {
    font-size: 25px;
    line-height: 1.4;
}

#img1 {
    width: 50em;
    padding: 60px;
    border-radius: 50%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


